.SectionContainer {

  > .sectionHeader {
    display: flex;
    align-items: center;
    padding-left: 0.5rem;
    cursor: default;
  }

  &.clickable {
    cursor: pointer;
    user-select: none;
    &:hover, &:active, &:focus {
      >.clickableGlass {
        display: block;
      }
    }
  }

  > .clickableGlass {
    pointer-events: none;
    position: absolute;
    top: 0;
    left: 0;
    bottom: 0;
    right: 0;
    z-index: 100;
    background-color: rgba(0,0,0,0.02);
    display: none;
  }


}
