.SwitchComponent {
  position: relative;
  min-height: 3rem;
  color: white;

  .slider {
    position: absolute;
    cursor: pointer;
    top: 0.4rem;
    left: 0;
    right: 0;
    bottom: 0.4rem;
    background-color: #ccc;
    -webkit-transition: .4s;
    transition: .4s;
    border-radius: 10rem;
    user-select: none;
    border: none;

    &:focus {
      outline: 1px solid #20b599 !important;
    }

    .circle {
      position: absolute;
      top: 50%;
      left: 50%;
      bottom: 0.2rem;
      background-color: white;
      -webkit-transition: .2s linear;
      transition: .2s linear;
      border-radius: 50%;
      transform: translate(-50%, -50%);
      display: inline-block;
      height: 62%;

      img {
        height: 100%;
        visibility: hidden;
      }
    }

    .yesLabel, .noLabel {
      position: absolute;
      top: 50%;
      transform: translate(0, -50%);
      padding: 0.2rem 1rem 0;
      opacity: 0;
      transition: opacity 0.2s;
    }

    .yesLabel {
      left: 0;
    }
    .noLabel {
      right: 0;
    }

    &.deny {
      background-color: $theme-color-error;
      .circle {
        transform: translate(31%, -50%);
        left: 0;
      }

      .noLabel {
        opacity: 1;
      }
    }

    &.undefined {
      background-color: $theme-main-header-icon-color;
    }

    &.confirm {
      background-color: $theme-color-ok;

      .circle {
        transform: translate(-131%, -50%);
        left: 100%;
      }

      .yesLabel {
        opacity: 1;
      }
    }
  }

  &.readonly {
    .slider {
      color: $theme-disabled-font-color;
      background-color: $theme-background-color;

      &.undefined {
        .circle {
          background-color: $theme-main-header-icon-color;
        }
      }

      &.deny {
        .circle {
          background-color: $theme-color-error;
        }
      }

      &.confirm {
        .circle {
          background-color: $theme-color-ok;
        }
      }
    }
  }
}
