my-table-container {
  .TableContainer {

    position: relative;
    width: 100%;
    height: 100%;
    overflow: auto;

    background-color: white;
    // border: 1px solid #ddd;
    // box-shadow: 0 0 0.3rem rgba(0, 0, 0, 0.08);

    > .sectionHeader {
      display: flex;
      align-items: center;
      padding-left: 0.5rem;
      cursor: default;
    }

    &.withHeader {
    }

    > .tableEntriesScroll {

      > .tableEntries {

        display: table;
        width: 100%;

        > .tableEntryHeader {
          display: table-row;
           > .tableColumnHeader {
             display: table-cell;
           }

          > .deleteRowColumnHeader {
            display: table-cell;
          }

          > .clickableGlassCell {
            width: 0;
          }
        }

        > .tableEntryRow {

          display: table-row;
          position: relative;

          &.clickable {
            cursor: pointer;
            user-select: none;
            &:hover, &:active, &:focus {
              > .clickableGlassCell {
                > .clickableGlass {
                  visibility: visible;
                }
              }
            }
          }

          > .clickableGlassCell {
            width: 0;
            > .clickableGlass {
              pointer-events: none;
              position: absolute;
              top: 0;
              left: 0;
              bottom: 0;
              right: 0;
              z-index: 100;
              background-color: rgba(0, 0, 0, 0.02);
              visibility: hidden;
            }
          }

          > .tableEntryCell {
            display: table-cell;
            vertical-align: middle; // Fix css table-cell extra paddings issue

            > .ViewComponent {
              flex-grow: 1;

              .componentBodyContainer {

                display: flex;
                align-content: stretch;
                align-items: stretch;

                > * { // angular component tag
                  flex-grow: 1;
                  display: flex;
                  align-content: stretch;
                  align-items: stretch;
                }

              }
            }

          }
        }

        .deleteRow {
          text-align: right;
        }

        .deleteRowColumnHeader {
          min-width: 1.5rem;
        }

        .deleteButton {
          // @include lightIconButton(1.5rem, 1.3rem);
          background-color: transparent;
          border: none;
          color: $theme-light-text-button-color;
          width: 1.5rem;
          height: 1.5rem;
          text-align: center;
          font-size: 1.3rem;
          padding: 0 0 0 0;

          &:hover, &:active, &:focus {
            color: $theme-default-font-color;
          }

          margin-top: 0.7rem;
        }

      }
    }

    .addButtonPanel {

      padding: 1rem 1.5rem;
      text-align: center;

      .addButton {
        background-color: white;
        border: 1px solid $theme-input-border-color;
        padding: 0.5rem 1.1rem 0.2rem;
        border-radius: 0.5rem;

        i {
          font-size: 1.4rem;
          line-height: 0.1rem;
          position: relative;
          top: 0.3rem;
          left: -0.5rem;
        }

        &:hover, &:active, &:focus {
          background-color: $theme-input-hover-background-color;
        }
      }
    }
  }
}
