.dropListPopup {

  background: white;
  border: 1px solid $theme-input-border-color;
  box-shadow: 0 1.5rem 2rem $theme-default-shadow-color;
  color: $theme-default-font-color;

  .filterContainer {
    position: relative;
    overflow: hidden;
    height: 2.3rem;
    margin: 0.3rem 0.4rem;
    flex-grow: 0;
    flex-shrink: 0;

    input {
      position: absolute;
      width: 100%;
      height: 100%;
      border: 1px solid #ddd;
      padding: 0.2rem 0.8rem;
      outline: none;
    }

    .clearFilter {
      position: absolute;
      top: 0;
      right: 0;
      height: 100%;
      width: 1.5rem;
      border: none;
      background-color: transparent;
      color: $theme-lighter-font-color;
      i {
        font-size: 1.2rem;
      }

      &:hover, &:active, &:focus {
        color: $theme-default-font-color;
      }
    }
  }

  .options {
    max-height: 30rem;
    overflow: auto;
    flex-grow: 1;
    cursor: pointer;

    .option {
      font-size: 1.1rem;
      padding: 0.4rem 0.9rem 0.3rem;
      white-space: normal;

      &:hover {
        background-color: $theme-input-hover-background-color;
      }

      &.active {
        background-color: $theme-color-a;
        color: white;
      }
    }
  }

}
