.DropListComponent {
  width: 100%;
  height: 100%;
  cursor: pointer;
  position: relative;


  &.validationError {
    .dropListValue {
      border-color: $theme-color-error !important;;
      color: $theme-color-error !important;;
    }
  }

  &.focused {
    .dropListValue {
      border: 1px solid $theme-color-a !important;
    }
  }

  .dropListValue {
    border: 1px solid $theme-input-border-color;
    position: relative;
    background-color: white;
    box-shadow: inset 0 0.1rem 0.3rem rgba(0, 0, 0, 0.05);
    border-radius: 0.3rem;
    height: 100%;
    flex-grow: 1;
    padding: 0.1rem 0.5rem;

    &.disabled {
      background-color: $theme-disabled-input-background-color;
      cursor: inferit;
      box-shadow: none;
    }

    &.notSelected {
      color: $theme-lighter-font-color;
    }
  }

  &.preview {
    .dropListValue {
      border: 1px solid transparent;
      box-shadow: none;
      background-color: transparent;
      color: $theme-default-font-color;
      font-weight: bold;
      letter-spacing: -0.007rem;
    }
  }

}
