.DropListComponent {
  display: flex;
  flex-direction: column;

  .dropListValue {
    border: 1px solid $theme-input-border-color;
    display: flex;
    flex-grow: 1;
    resize: none;
    padding: 0.7rem 0.3rem 0.5rem;
    line-height: 1.4em;
    overflow: hidden;
    box-shadow: inset 0 0.1rem 0.3rem rgba(0, 0, 0, 0.05);
    border-radius: 0.3rem;
    outline: 0;
    background-color: white;

    &:focus {
      transition: border-color 0.15s ease-in-out 0s, box-shadow 0.15s ease-in-out 0s, background-color 0.2s;
      border-color: #20b599;
    }

  }

  .expandArrow {
    position: absolute;
    top: calc(50% + 0.3rem);
    right: 0.8rem;
    font-size: 1.4rem;
    transform: translateY(-50%);
    pointer-events: none;
  }

}
