#drop-menu-overlay {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  pointer-events: none;

  visibility: hidden;
  opacity: 0;
  transition: opacity 0.1s;

  > * {
    pointer-events: auto;
  }

  &.blockingVisible {
    visibility: visible;
    opacity: 1;
  }

}
