.TableContainer {

  border-radius: 0.1rem;
  overflow: hidden;
  border: 0 solid #ddd;
  display: flex;
  flex-direction: column;

  > .arrowsDragHandler {
    position: absolute;
    top: 0;
    right: 0;
    color: #8d8d90;
    z-index: 1;
    padding: 0.2rem;
    width: 1.7rem;
    height: 1.7rem;
    text-align: center;
    font-size: 1.2rem;
    background-color: rgba(255,255,255,0.9);
    box-shadow: 0 0 0.5rem rgba(255,255,255,0.9);
  }

  > .sectionHeader {
    flex-shrink: 0;
    height: 3rem;
    width: 100%;
    background-color: #fbfbfb;
    // border-bottom: 1px solid $theme-input-hover-border-color;
    border-bottom-width: 1px;
    border-bottom-style: solid;
    border-color: $theme-input-hover-border-color;
    padding: 0.3rem;
    display: flex;
    font-size: 1.2rem;
    flex-grow: 0;

    &.found {
      color: $theme-font-color-b;
    }
  }
  >.tableEntriesScroll {
    overflow: auto;

    >.tableEntries {


      grid-template-rows: auto;
      width: 100%;

      > .tableColumnHeader {
        .columnLabelText {
          padding: 0.3rem 0.2rem;
        }
      }

      .tableEntry {

        display: table-row !important;

        > .ViewComponent {
          display: table-cell !important;
          border: solid #e1e1e1;
          border-width: 0 0 1px 1px;
          vertical-align: top;
        }

      }
    }
  }

}
