.SingleCheckboxComponent {

  .checkboxEntry {
    display: block;
    min-height: 2em;
    min-width: 2em;
    position: relative;
    cursor: default;
    background-color: transparent;
    border: none;
    padding: 0.5em 0 0.2em 0;


    &.checked {

      .checkBoxSvg {

        .box {
          fill: #a8a8aa;
        }

        .background {
          fill: #9a9a9d;
        }

        .ticker {
          visibility: visible;
          fill: #fff !important;
          stroke: #fff !important;
          stroke-miterlimit:10;
        }

      }

      .checkboxLabel {
        color: $theme-default-font-color;
      }

    }

    .checkBoxSvg {
      position: absolute;
      top: 0.5rem;
      left: 0.2rem;
      width: 1.6em;
      height: 1.6em;
      display: inline-block;
      vertical-align: middle;

      .box {
        fill: #eee;
      }

      .background {
        fill: #dee1e3;
      }

      .ticker {
        visibility: hidden;
      }

      .unknown {
        visibility: hidden;
      }
    }

    .checkboxLabel {
      vertical-align: top;
      color: $theme-lighter-font-color;
      margin: 0 1em 0 2em;
      white-space: normal;
      line-height: 1.77em;
      text-align: left;
    }

  }

  &.enabled {
    .checkboxEntry {
      cursor: pointer;

      &:focus {
        .checkBoxSvg {

          .background {
            fill: #bbb;
          }
        }

        .checkboxLabel {
          color: $theme-medium-font-color;
        }
      }

      &:hover {
        .checkBoxSvg {

          .background {
            fill: #bbb;
          }
        }

        .checkboxLabel {
          color: $theme-medium-font-color;
        }
      }

      &:focus {
        .checkBoxSvg {

          .background {
            fill: $theme-button-color-a;
          }

        }

        .checkboxLabel {
          color: $theme-medium-font-color;
        }
      }

      .checkBoxSvg {
        .box {
          fill: #fff;
        }
      }

      &.checked {

        .checkBoxSvg {

          .box {
            fill: $theme-button-color-a;
          }

          .background {
            fill: $theme-button-color-a;
          }
        }

        &:hover {
          .checkBoxSvg {

            .background {
              fill: #777;
            }
          }

          .checkboxLabel {
            color: $theme-default-font-color;
          }
        }

        &:focus {
          .checkBoxSvg {

            .background {
              fill: #777;
            }
          }

          .checkboxLabel {
            color: $theme-default-font-color;
          }
        }
      }

      &.unknown {
        .checkBoxSvg {

          .unknown {
            visibility: visible;
            stroke-miterlimit: 10;
            fill: #ccc;
            stroke: white;
            font-size: 11px;
            stroke-width: 0.5px;
          }
        }
      }

      &.invalid {
        .checkBoxSvg {

          .background {
            fill: $theme-color-error !important;
          }
        }

        .checkboxLabel {
          color: $theme-color-error !important;
        }
      }
    }
  }



}

