.screenComponentLabel {
  display: flex;

  min-width: 100%; // set by JS and label position
  min-height: 1rem; // set by JS and label position

  &.top {
    bottom: calc(100% + 0.3rem);
    left: 0;
    text-align: left;
    padding-bottom: 0.2rem;
    padding-left: 0.35rem;
    padding-right: 0.5rem;
    width: 100%;
  }
  &.right {
    left: calc(100%);
    top: 0;
    bottom: 0;
    text-align: left;
    padding-left: 0.6rem;
  }
  &.left {
    right: calc(100%);
    top: 0;
    bottom: 0;
    text-align: right;
    padding-right: 0.6rem;
  }
  &.bottom {
    top: calc(100% + 0.3rem);
    left: 0;
    text-align: left;
    padding-top: 0.2rem;
    padding-left: 0.35rem;
    padding-right: 0.5rem;
    width: 100%;
  }
  &.free {
    top: 0;
    left: 0;
  }
}
