.DateInputCommon {
  display: flex;
  flex-direction: row;
  flex-wrap: nowrap;
  border: 1px solid $theme-input-border-color;
  border-radius: 0.3rem;

  &.tableCellMode {
    //.input {
    //  border: none;
    //  box-shadow: none;
    //}
  }

  .input {
    display: block;
    width: 1px; // it will grow
    flex-grow: 1;
    padding: 0.7rem 0.3rem 0.5rem;
    box-shadow: inset 0 0.1rem 0.3rem rgba(0, 0, 0, 0.05);
    background-color: white;
    outline: 1px solid transparent;
    outline-offset: -1px;
    border: 0;
    border-radius: 0.3rem;

    &:focus {
      transition: outline-color 0.15s ease-in-out 0s, box-shadow 0.15s ease-in-out 0s, background-color 0.2s;
      outline-color: #20b599;
    }
  }
  &.disabled {
    border: 1px solid #f0f0f0;
    .input {
      background-color: #fcfcfc;
      box-shadow: none;
      cursor: inherit;
    }
  }

  &.preview {
    border-color: transparent;
    .input {
      background-color: transparent;
      border-color: transparent;
    }
    .inputHeader {
      border-color: transparent;
      background-color: transparent;
      padding-left: 0.3rem;
      width: 1.85rem;
    }
  }

  .inputHeader {
    border-right: 1px solid $theme-input-border-color;
    font-size: 1.3rem;
    background-color: #f5f7f6;
    padding: 0.45rem 0.85rem 0.45rem 0.85rem;
    display: inline-block;
    height: 100%;
    width: 3rem;
    border-radius: inherit;
  }

  .calendarIcon {
    width: 1.2rem;
    height: 1.2rem;
    position: relative;
    top: 0.2rem;
  }

}
