$primaryHeight: 3.1rem;
$secondaryHeight: 2.3rem;

@mixin input-primary-height() {
  height: $primaryHeight !important;
  font-size: 1rem;
}

.inputPrimaryHeight {
  height: $primaryHeight !important;
  font-size: 1rem;
}

@mixin input-secondary-height() {
  height: $secondaryHeight !important;
  font-size: 1rem;
}

.inputSecondaryHeight {
  height: $secondaryHeight !important;
  font-size: 1rem;
}

@mixin placeholder($color, $size:"", $bold: "") {
  &::-webkit-input-placeholder {
    color: $color;
    @if $size != "" {
      font-size: $size;
    }
    @if $bold != "" {
      font-weight: bold;
    }
  }
  &:-moz-placeholder {
    color: $color;
    @if $size != "" {
      font-size: $size;
    }
    @if $bold != "" {
      font-weight: bold;
    }
  }
  &::-moz-placeholder {
    color: $color;
    @if $size != "" {
      font-size: $size;
    }
    @if $bold != "" {
      font-weight: bold;
    }
  }
  &:-ms-input-placeholder {
    color: $color;
    @if $size != "" {
      font-size: $size;
    }
    @if $bold != "" {
      font-weight: bold;
    }
  }
}

.placeholder {
  color: #aaaaaa;
}
