.NumberInputComponent {
  display: flex;
  align-items: stretch;

  .input {
    border: 1px solid #e5e6e7;
    resize: none;
    padding: 0.7rem 0.3rem 0.5rem; // padding right should be set in controller also
    line-height: 1.4em;
    overflow: hidden;
    box-shadow: inset 0 0.1rem 0.3rem rgba(0, 0, 0, 0.05);
    border-radius: 0.3rem;
    outline: 0;
    background-color: white;
    width: 1px; // it will grow
    flex-grow: 1;

    &::placeholder {
      color: $theme-placeholder-font-color;
    }

    &:focus {
      transition: border-color 0.15s ease-in-out 0s, box-shadow 0.15s ease-in-out 0s, background-color 0.2s;
      border-color: #20b599;
    }

    &.disabled, &:disabled {
      border: 1px solid #f0f0f0;
      background-color: #fcfcfc;
      box-shadow: none;
      cursor: default;
    }
  }

  &.tableCellMode {
    //.input {
    //  border: none;
    //  box-shadow: none;
    //}
  }

  .unitName {
    right: 0;
    padding-left: 0.3em;
    padding-right: 0;
    top: 0;
    opacity: 0.8;
    font-size: 0.9em;
    pointer-events: none;
    display: flex;
    height: 100%;
    align-items: flex-start;

    &.singleline {
      padding-top: unset !important;
      align-items: center;
    }
  }


  &.preview {
    .input {
      border: 1px solid transparent;
      box-shadow: none;
      background-color: transparent;
      color: $theme-default-font-color;
      font-weight: bold;
      letter-spacing: -0.007rem;
    }
  }

}
