.ImageComponent {
  height: 100%;
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  object-fit: cover; /* make it customizable in design (contain or cover)*/

  img {
    max-width: 100%;
    max-height: 100%;
  }

  &.emptySource {
    background-color: #fbfbfb;

    .imageContent {
      position: absolute;
      width: 100%;
      height: 100%;
      display: flex;
      justify-content: center;
      align-items: center;
    }
  }
}
