.SingleCheckboxComponent {
  &.validationError {
    .checkboxLabel {
      color: $theme-color-error !important;;
    }
  }

  .checkboxLabel {
    .requiredIcon {
      margin-left: 0.3rem;
      font-size: 1.8em;
      position: relative;
      top: 0.35em;
      opacity: 0.6;
      user-select: none;
      line-height: 1px;
    }
  }

}

