.TabsContainer {

  border-radius: 0.1rem;
  overflow: hidden;
  border: 0 solid #ddd;

  > .tabsHeader {
    min-height: 3rem;
    font-size: 1.2rem;
    background-color: #fbfbfb;
    // border-bottom: 1px solid $theme-input-hover-border-color;
    border-bottom-width: 1px;
    border-bottom-style: solid;
    border-color: $theme-input-hover-border-color;

    display: flex;

    .tabHeader {
      display: inline-flex;
      position: relative;
      padding: 0.3rem;
      text-align: center;
      border-color: transparent;

      &.selected {
        border-bottom: 2px solid $theme-color-a;
      }

      &.disabled {
        opacity: .5;
        cursor: default;
        .tab {
          cursor: default;
        }
      }

      &:hover, &:active, &:focus {
        &:not(.disabled) {
          color: $theme-font-color-a;
        }
      }

      .tab {
        background-color: transparent;
        font-size: 1.1rem;
        padding: 0.3rem 0.6rem;
        border: none;
        text-align: inherit;
        color: inherit;
      }

    }

  }
  > .childrenPlain {
    min-height: calc(100% - 3rem);
  }
}
