my-toastr-overlay {
	z-index: 1000000;
	position: fixed;
	right: 1rem;
	top: 1rem;

  &.bottom {
    bottom: 6.2rem;
    top: unset;
    right: 1.5rem;
  }

	.toastr {
		color: white;
		margin: 1rem 0;
		padding: 1rem 2.4rem 1rem 3rem;
		border-radius: 0.3em;
		width: 30rem;
		min-height: 4rem;
		opacity: 0.8;
		box-shadow: 0.1rem 0.1rem 1rem rgba(0, 0, 0, 0.7);
		transition: box-shadow 0.2s;
		white-space: pre-wrap;
    position: relative;
    cursor: default;

    &:first-child {
      margin-top: 0;
    }

    &:last-child {
      margin-bottom: 0;
    }

    i.icon {
      color: white;
      font-size: 1.9rem;
      position: absolute;
      top: 50%;
      left: 0.5rem;
      transform: translateY(-45%);
    }

		&:hover {
			opacity: 1;
			transition: box-shadow 0.1s;
			box-shadow: 0.1rem 0.1rem 1rem rgba(0, 0, 0, 0.9);
		}

		&.info {
			background-color: $theme-color-info;
		}

		&.warning {
			background-color: $theme-color-warning;
		}

		&.error {
			background-color: $theme-color-error;
		}

		&.success {
			background-color: $theme-color-ok;
		}

		.closeToastr {
      position: absolute;
      top: 0;
      right: 0;
      bottom: 0;
      width: 2.4rem;
      opacity: 0.7;
      background-color: transparent;
      border: none;

      &:hover {
        opacity: 1;
      }

      i {
        position: absolute;
        top: 0.3rem;
        right: 0.3rem;
        pointer-events: none;
        font-size: 1.5rem;
        color: white;
      }


    }

  }

}
