.SingleAttachmentInputComponent {

  &.validationError {
    border-color: $theme-color-error !important;
  }

  .fileUploadDisplay {
    .uploadProgress {
      position: absolute;
      top: 0;
      left: 0;
      bottom: 0;
      background-color: $theme-color-a-light;
      opacity: 0.3;
      pointer-events: none;
    }

    .dropAreaGlass {
      display: none;
      border: 2px dashed $theme-drop-area-border-color;
      background: $theme-drop-area-background-color;
      position: absolute;
      top: -0.3rem;
      left: -0.35rem;
      right: -0.3rem;
      bottom: -0.3rem;
      pointer-events: none;
      z-index: 1000;

      .dropInstruction {
        font-size: 2rem;
        color: $theme-drop-area-message-color;
        position: absolute;
        left: 50%;
        top: 50%;
        -ms-transform: translate(-50%, -50%);
        -moz-transform: translate(-50%, -50%);
        -webkit-transform: translate(-50%, -50%);
        transform: translate(-50%, -50%);
      }
    }

    &.fileDropZone {
      .dropAreaGlass {
        display: block;
      }
    }

    &.fileDragged {
      .dropAreaGlass {
        display: block;
        background: $theme-drop-area-hover-background-color;
      }
    }
  }

  .uploadSection {
    .uploadButton {
      position: absolute;

      &:hover, &:active, &:focus {
        background-color: $theme-input-hover-background-color;
        color: $theme-default-font-color;
      }
    }
  }

  .downloadSection {
    text-align: right;
    flex-grow: 0;
    .downloadButton {

      display: inline-block;
      border: none;
      background-color: transparent;
      font-size: 1rem;
      color: $theme-lighter-font-color;
      padding: 0.7rem 0.1rem 0.7rem 1rem;
      text-decoration: none;

      &:hover, &:active, &.focus {
        color: $theme-default-font-color;
      }
      i {
        font-size: 1.7rem;
        line-height: 1rem;
        position: relative;
        top: 0.4rem;
        margin-right: 0.5rem;
      }
    }


  }

  .deleteButton, .cancelButton {
    position: absolute;
    background: transparent;
    border: none;
    color: $theme-lighter-font-color;
    &:hover, &:active, &:focus {
      color: $theme-default-font-color;
    }
  }

  .deleteButton {
    right: 0.4rem;
    top: 0.8rem;
    font-size: 1.4rem;
  }

  .cancelButton {
    right: 0.1rem;
    top: 0.4rem;
    font-size: 2rem;
  }

}
