html, body, div, span, applet, object, iframe, h1,
h2, h3, h4, h5, h6, p, blockquote, pre, a, abbr, acronym, address, big, cite, code, del, dfn, em, font, ins, kbd, q, s, samp, small,
strike, strong, sub, sup, tt, var, dl, dt, dd, ol, ul, li, fieldset, form, label, legend, table, caption, tbody, tfoot, thead, tr, th, td {
  font-family: myDefaultFont, sans-serif;
}


/* Tabs */
ul.nav {
  margin-bottom: 30px;
}


.deleteIcon {
  cursor: pointer;
  line-height: 2em;
}


/* Toastr */

#toast-container > div {
  opacity: 1;
}

.ibox {
  box-shadow: 2px 4px 4px rgba(0, 0, 0, 0.11);
  border-left: 1px solid #ddd;
  border-right: 1px solid #ddd;
}

.btn {
  &.btn-success {
    background-color: $theme-button-color-b;
    border-color: $theme-button-color-b;
  }

  &.btn-warning {
    background-color: #f49c14;
    border-color: #f49c14;
  }

  &.btn-primary {
    background-color: $theme-button-color-a;
    border-color: $theme-button-color-a;

    &.active {
      background-color: darken($theme-color-a, 4%);
      border-color: darken($theme-color-a, 4%);
    }
  }
}


