.MapComponent {
  width: 100%;
  height: 100%;
  position: relative;

  .map-container {
    width: unset !important;
    height: 100% !important;
  }
  .mapContent {
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
  }
  .mapPointer {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    color: red;
    font-size: 2rem;
    height: 2rem;
    pointer-events: none;
  }
}
