.RepeatableContainer {

  border-radius: 0.1rem;
  border: 0 solid #ddd;
  display: flex;
  flex-direction: column;

  > .arrowsDragHandler {
    position: absolute;
    top: 0;
    right: 0;
    color: #8d8d90;
    z-index: 1;
    padding: 0.2rem;
    width: 1.7rem;
    height: 1.7rem;
    text-align: center;
    font-size: 1.2rem;
    background-color: rgba(255,255,255,0.9);
    box-shadow: 0 0 0.5rem rgba(255,255,255,0.9);
  }

  > .sectionHeader {
    flex-shrink: 0;
    height: 3rem;
    width: 100%;
    background-color: #fbfbfb;
    font-size: 1.2rem;
    flex-grow: 0;
    // border-bottom: 1px solid $theme-input-hover-border-color;
    border-bottom-width: 1px;
    border-bottom-style: solid;
    border-color: $theme-input-hover-border-color;
    display: flex;
    padding: 0.3rem;

    &.found {
      color: $theme-font-color-b;
    }
  }
  >.repeatableEntriesScroll {
    overflow: auto;
    flex: none;
    >.repeatableEntries {
      //min-height: 100%;

      .repeatableEntry {
        min-height: 100%;
        > .childrenPlain {
          position: relative;
          min-height: 100%;
        }
      }
    }
  }

  &.entriesLayout_vertical {

    >.repeatableEntriesScroll {
      > .repeatableEntries {
        width: 100% !important;
        display: flex;
        flex-direction: column;
        flex-wrap: nowrap;
        align-content: stretch;
        overflow-y: hidden; // make it auto if max height is specified
        overflow-x: hidden;

        > .repeatableEntry {
          width: 100% !important;
        }
      }
    }

  }

  &.entriesLayout_horizontal {
    >.repeatableEntriesScroll {
      > .repeatableEntries {
        display: flex;
        flex-direction: row;
        flex-wrap: wrap; // might change
        align-content: flex-start; // might change
        overflow-y: hidden;
        overflow-x: hidden; // make it auto if max width is specified
        .repeatableEntry {
        }
      }
    }
    &.entriesLayoutWrap_wrap {
      >.repeatableEntriesScroll {
        .repeatableEntries {
          flex-wrap: wrap;
        }
      }
    }

    &.entriesLayoutWrap_noWrap {
      >.repeatableEntriesScroll {
        .repeatableEntries {
          flex-wrap: nowrap;
        }
      }
    }

  }

}
