.PasswordInputComponent {

  display: flex;
  align-items: stretch;

  .input {
    border: 1px solid #e5e6e7;
    display: block;
    resize: none;
    padding: 0.7rem 0.3rem 0.5rem;
    line-height: 1.4em;
    overflow: hidden;
    box-shadow: inset 0 0.1rem 0.3rem rgba(0, 0, 0, 0.05);
    border-radius: 0.3rem;
    outline: 0;
    background-color: white;
    width: 1px; // it will grow
    flex-grow: 1;

    &::placeholder {
      color: $theme-placeholder-font-color;
    }

    &:focus {
      border-color: #20b599;
      transition: border-color 0.15s ease-in-out 0s, box-shadow 0.15s ease-in-out 0s, background-color 0.2s;
    }

    &.disabled, &:disabled {
      border: 1px solid #f0f0f0;
      background-color: #fcfcfc;
      box-shadow: none;
      cursor: default;
    }

  }

  &.tableCellMode {
    //.input {
    //  border: none;
    //  box-shadow: none;
    //}
  }

  &.preview {
    .input {
      border: 1px solid transparent;
      box-shadow: none;
      background-color: transparent;
      color: $theme-default-font-color;
      font-weight: bold;
      letter-spacing: -0.007rem;
    }
  }

}
