.SingleAttachmentInputComponent {
  min-height: 7.2rem;
  border-radius: 0.3rem;
  border: 1px solid $theme-input-border-color;
  display: flex;
  background-color: white;
  overflow: hidden;

  &.disabled {
    border: 1px solid #f0f0f0;
    background-color: $theme-disabled-input-background-color;
    .fileUploadDisplay {
      box-shadow: none;
    }
  }

  &.preview {
    border: 1px solid transparent;
    box-shadow: none;
    background-color: transparent;
  }


  .dragArea {
    flex-grow: 1;
    display: flex;
    flex-direction: row;
    flex-wrap: nowrap;
  }

  .inputHeader {
    background-color: $theme-background-color;
    width: 3rem;
    padding-top: 0.7rem;
    font-size: 1.5rem;
    text-align: center;
    border-right: 1px solid $theme-input-border-color;
  }

  .filesContainer {
    flex-grow: 1;

    .singleAttachment {
      position: relative;

      .fileInfo {
        display: block;
        padding: 0.7rem 0.8rem;
        font-size: 0.9rem;
        color: $theme-lighter-font-color;

        .uploadedSize {
          color: $theme-default-font-color;
        }

        .separator {
          font-size: 1.2rem;
          margin: 0 0.2rem;
          line-height: 1rem;
        }

        .fileName {
          text-decoration: none;
          font-size: 1.3rem;
          padding: 0.6rem 0 1.3rem;
          display: block;
          color: $theme-font-color-b;

          i {
            font-size: 2.5rem;
            line-height: 1rem;
            position: relative;
            top: 0.8rem;
            margin-right: 0.5rem;
          }
        }

      }

      .flowInfo {
        padding: 0.4rem 1.1rem 0.8rem;
        font-size: 0.9rem;
        color: $theme-lighter-font-color;

        .separator {
          font-size: 1.2rem;
          margin: 0 0.2rem;
          line-height: 1rem;
        }

        .namePart {
          border: none;
          background-color: transparent;
          padding: 1.3rem 0.8rem;
          font-size: 1.3rem;
          color: $theme-font-color-b;

          i {
            font-size: 2.5rem;
            line-height: 1rem;
            position: relative;
            top: 0.8rem;
            margin-right: 0.5rem;
          }
        }
      }

      .unknownInfo {
        padding: 0.4rem 1.1rem 0.8rem;
        font-size: 0.9rem;
        color: $theme-lighter-font-color;
      }

      .emailInfo {
        padding: 0.4rem 1.1rem 0.8rem;
        font-size: 0.9rem;
        color: $theme-lighter-font-color;

        .separator {
          font-size: 1.2rem;
          margin: 0 0.2rem;
          line-height: 1rem;
        }

        .namePart {
          border: none;
          background-color: transparent;
          padding: 1.3rem 0.8rem;
          font-size: 1.3rem;
          color: $theme-font-color-b;

          i {
            font-size: 2.5rem;
            line-height: 1rem;
            position: relative;
            top: 0.8rem;
            margin-right: 0.5rem;
          }
        }
      }

    }
  }



  .uploadSection {
    text-align: right;
    // margin-top: 0.3rem;
    flex-grow: 0;

    .uploadButton {

      display: inline-block;
      border: none;
      background-color: transparent;
      font-size: 1rem;
      padding: 0.7rem 1rem;
      color: $theme-lighter-font-color;

      i {
        font-size: 1.7rem;
        line-height: 1rem;
        position: relative;
        top: 0.4rem;
        margin-right: 0.5rem;
      }
    }

  }

  .fileUploadDisplay {
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: column;
    box-shadow: inset 0 0.1rem 0.3rem rgba(0, 0, 0, 0.05);
  }

  &.minimal {
    .fileUploadDisplay {
      flex-direction: row;
      flex-wrap: nowrap;
      justify-content: stretch;
      .filesContainer {
        .singleAttachment {
          &:last-child {
            border-bottom: none;
          }

          .fileInfo {
            padding: 0;
          }

          .fileName {
            text-decoration: none;
            font-size: 1.1rem;
            padding: 0.7rem 0.8rem;
            display: inline-block;
            color: $theme-font-color-b;


            i.fileIcon {
              font-size: 1.8rem;
              line-height: 1rem;
              position: relative;
              top: 0.6rem;
              margin-right: 0.5rem;
            }
          }

          .details {
            font-size: 0.9rem;
            margin-left: 1rem;
            color: $theme-lighter-font-color;
            .separator {
              margin: 0 0.5rem;
            }
          }

        }
      }

      .uploadSection {
        margin-top: 0;
        border-left: 1px solid $theme-box-border-color;
        .uploadButton {
          width: 100%;
          height: 100%;
          padding: 0 0.2rem;
          i {
            margin-right: 0;
          }
        }
      }

    }
  }


}
