.view {
  @import "components/dateInputCommon";
  @import "components/textInput";
  @import "components/numberInput";
  @import "components/passwordInput";
  @import "components/dropList";
  @import "components/radioButton";
  @import "components/multiCheckbox";
  @import "components/singleCheckbox";
  @import "components/button";
  @import "components/label";
  @import "components/personSelect";
  @import "components/dateInput";
  @import "components/dateTimeInput";
  @import "components/timeInput";
  @import "components/durationInput";
  @import "components/link";
  @import "components/html";
  @import "components/image";
  @import "components/componentLabel";
  @import "components/switch";
  @import "components/multiAttachmentInput";
  @import "components/singleAttachmentInput";
  @import "containers/section";
  @import "containers/repeatable";
  @import "containers/table";
  @import "containers/tabs";

  div, span, applet, object, iframe, h1,
  h2, h3, h4, h5, h6, p, blockquote, pre, a, abbr, acronym, address, big, cite, code, del, dfn, em, font, ins, kbd, q, s, samp, small,
  strike, strong, sub, sup, tt, var, dl, dt, dd, ol, ul, li, fieldset, form, label, legend, table, caption, tbody, tfoot, thead, tr, th, td {
    font-family: inherit;
  }

  .viewCanvas {

    position: relative;
    // for root
    display: flex;
    flex-direction: row;
    justify-content: stretch;
    align-content: center;
    > .ViewComponent {
      position: relative;
      top: unset !important;
      left: unset !important;
      margin-left: auto !important;
      margin-right: auto !important;
    }
  }

  .requiredIcon {
    margin-left: 0.3rem;
    font-size: 1.8em;
    position: relative;
    top: 0.35em;
    opacity: 0.6;
    user-select: none;
    line-height: 1px;
  }

  .childrenPlain {
    &.layout_vertical, &.layout_horizontal, &.layout_grid {
      > .ViewComponent {
        > .componentBodyContainer {

          .componentLabel {
            position: relative;
          }

          > * { // angular component tag
            width: 100%;
            height: 100%;
            flex-direction: column;
            display: flex;
            justify-content: stretch;
          }

          &.labelPosition_start {
            > * {
              flex-direction: row;
              display: flex;
              justify-content: stretch;
            }
          }

          &.labelPosition_top {
            > * {
              flex-direction: column;
              display: flex;
              justify-content: stretch;
            }
          }

          &.labelPosition_bottom {
            > * {
              flex-direction: column-reverse;
              display: flex;
              justify-content: stretch;
            }
          }

          &.labelPosition_end {
            > * {
              flex-direction: row-reverse;
              display: flex;
              justify-content: stretch;
            }
          }

          .componentLabel {
            flex-grow: 0;
          }

          .componentBody {
            flex-grow: 1;
            position: relative;
          }

        }

      }
    }

    &.layout_static {
      > .ViewComponent {
        > .componentBodyContainer {


          position: relative;

          .componentLabel {
            position: relative;
          }

          > * { // angular component tag
            width: 100%;
            height: 100%;
          }

          &.labelPosition_start {

            .componentLabel {
              position: absolute;
              right: 100%;
              height: 100%;
            }

          }

          &.labelPosition_top {

            .componentLabel {

              position: absolute;
              bottom: 100%;
              width: 100%;
            }

          }

          &.labelPosition_bottom {

            .componentLabel {

              position: absolute;
              top: 100%;
              width: 100%;
            }

          }

          &.labelPosition_end {

            .componentLabel {
              position: absolute;
              left: 100%;
              height: 100%;
            }

          }
        }
      }
    }
  }

  .ViewComponent {

    //height: 1px; // 1px is to force browsers to to work when parent has min-height/min-width set and child height/width of 100%, https://stackoverflow.com/questions/8468066/child-inside-parent-with-min-height-100-not-inheriting-height
    //width: 1px; // INFO I'll try to set it in static container only

    display: flex;
    align-content: stretch;
    align-items: stretch;
    position: relative;

    > .otherScreenReferenced {
      position: absolute;
      top: 0;
      left: 0;
      right: 0;
      bottom: 0;
      background-color: rgba(228,240,252,0.1);
      pointer-events: none;
      z-index: 1000;
      border-radius: 0.2rem;
      border: 2px solid rgba(63,119,226,0.2);
    }

    &.layout_static {

    }

    &.layout_vertical {
      height: unset !important;
    }

    &.layout_horizontal {
      height: unset !important;
    }

    &.layout_grid {
      //height: unset !important; minimum height is required
    }

    &.selected {
      > .componentBodyContainer {

      }

    }


    my-screen-component {
      flex-grow: 1;
    }

    .componentBodyContainer {
      flex-grow: 1;

      &.labelPosition_start {


        .screenComponentLabel {
          justify-content: right;
          text-align: right;
          min-height: 100%;
          padding-right: 0.6rem;
          padding-top: 0.7rem;
          padding-bottom: 0.7rem;
        }
      }

      &.labelPosition_top {

        .screenComponentLabel {

          justify-content: left;
          text-align: left;
          align-items: start;
          min-width: 100%;

          padding-bottom: 0.2rem;
          padding-left: 0.35rem;
          padding-right: 0.5rem;
        }
      }

      &.labelPosition_bottom {

        .screenComponentLabel {


          justify-content: left;
          text-align: left;
          align-items: end;
          min-width: 100%;

          padding-bottom: 0.2rem;
          padding-left: 0.35rem;
          padding-right: 0.5rem;
        }
      }

      &.labelPosition_end {

        .screenComponentLabel {
          justify-content: left;
          text-align: left;
          min-height: 100%;
          padding-left: 0.6rem;
          padding-top: 0.7rem;
          padding-bottom: 0.7rem;
        }
      }

    }


    > .componentSelector {
      position: absolute;
      top: 0;
      left: 0;
      right: 0;
      bottom: 0;
      border: 1px solid transparent;
      pointer-events: none;
      z-index: 10;
    }
    &.dragged {
      > * {
        visibility: hidden;
      }
      > .componentSelector {
        background-color: rgba(0, 0, 0, 0.025);
        border-radius: 0.3rem;
        visibility: visible;
      }
    }

    .minSizeIndicator {
      position: absolute;
      top: 0;
      left: 0;
      z-index: 1;
      pointer-events: none;
      border: solid rgba(81, 141, 129, 0.6);
      border-width: 2px 0 0 2px;
    }

    &.found {
      z-index: 1;
    }

    &.selected {
      z-index: 2;
    }



  }



  .childrenPlain {
    position: relative;
    &.layout_vertical {
      display: flex;
      flex-direction: column;
      flex-wrap: nowrap;
      align-content: stretch;
      > .ViewComponent {
        position: relative;

        width: unset !important;

        &.layout_static, &.layout_vertical, &.layout_horizontal {
          height: unset !important;
        }

        top: unset !important;
        left: unset !important;
        right: unset !important;
        bottom: unset !important;
        flex-grow: unset !important;

        .minSizeIndicator {
        }
      }
    }

    &.layout_horizontal {
      display: flex;
      flex-direction: row;
      flex-wrap: wrap; // might change
      align-content: flex-start; // might change
      overflow-y: hidden;
      overflow-x: auto; // might change
      > .ViewComponent {
        position: relative;

        &.layout_static, &.layout_vertical, &.layout_horizontal {
          width: unset !important;
        }

        top: unset !important;
        left: unset !important;
        right: unset !important;
        bottom: unset !important;
      }
    }


    &.layout_grid {
      display: grid;
      > .ViewComponent {
        position: relative;
        top: unset !important;
        left: unset !important;
        right: unset !important;
        bottom: unset !important;
        margin-top: unset !important;
      }
    }

    &.layout_static {
      > .ViewComponent {
        position: absolute;
        margin: unset !important;
        flex-grow: unset !important;
        grid-row-end: unset !important;
        grid-column-end: unset !important;

        //&.layout_static, &.layout_vertical, &.layout_horizontal {
        //  width: unset !important;
        //  height: unset !important;
        //}
      }
    }


    &.layout_horizontal {



      &.layoutAlign_start {
        justify-content: flex-start;

        > .ViewComponent {
          flex-grow: 0 !important;
        }

      }

      &.layoutAlign_center {
        justify-content: center;

        > .ViewComponent {
          flex-grow: 0 !important;
        }
      }

      &.layoutAlign_stretch {
        justify-content: stretch;

        > .ViewComponent {
          > .minSizeIndicator {
          }
        }
      }

      &.layoutAlign_end {
        justify-content: flex-end;

        > .ViewComponent {
          flex-grow: 0 !important;
        }
      }

      &.layoutStretch_start {
        align-items: flex-start;     // single line alignment
        align-content: flex-start; // content alignment

        > .ViewComponent {
          > .minSizeIndicator { // element is always 100% width and height is min-weight?
          }
        }
      }

      &.layoutStretch_stretch {
        align-items: stretch;    // single line alignment
        align-content: stretch;   // content alignment

        > .ViewComponent {
          height: unset !important;

          > .minSizeIndicator {
          }
        }


      }

      &.layoutStretch_center {
        align-items: center;    // single line alignment
        align-content: center;    // content alignment

        > .ViewComponent {
          > .minSizeIndicator {
          }
        }
      }

      &.layoutStretch_end {
        align-items: flex-end;    // single line alignment
        align-content: flex-end;  // content alignment

        > .ViewComponent {
          > .minSizeIndicator {
          }
        }
      }


      &.layoutWrap_wrap {
        flex-wrap: wrap;
      }

      &.layoutWrap_noWrap {
        flex-wrap: nowrap;
      }
    }

  }


}
