.RepeatableContainer {

  position: relative;
  width: 100%;
  height: 100%;

  > .sectionHeader {
    padding-left: 0.5rem;
    height: 3rem;
    width: 100%;
    background-color: #fbfbfb;
    font-size: 1.2rem;
    cursor: default;
    display: flex;
  }

  &.withHeader {
  }

  > .repeatableEntriesScroll {
    > .repeatableEntries {

      > .repeatableEntry {
        position: relative;


        &.clickable {
          cursor: pointer;
          user-select: none;
          &:hover, &:active, &:focus {
            >.clickableGlass {
              display: block;
            }
          }
        }

        > .clickableGlass {
          pointer-events: none;
          position: absolute;
          top: 0;
          left: 0;
          bottom: 0;
          right: 0;
          z-index: 100;
          background-color: rgba(0,0,0,0.02);
          display: none;
        }

        .deleteButton {

          background-color: transparent;
          border: none;
          color: $theme-light-text-button-color;
          width: 1.5rem;
          height: 1.5rem;
          text-align: center;
          font-size: 1.3rem;
          padding: 0 0 0 0;

          &:hover, &:active, &:focus {
            color: $theme-default-font-color;
          }
          position: absolute;
          top: 0.7rem;
          right: 0.2rem;
        }

      }
    }
  }

  &.entrisLayout_vertical {
    > .repeatableEntries {

      > .repeatableEntry {
        width: 100%;
      }
    }

  }

  &.entrisLayout_horizontal {
    > .repeatableEntries {

      > .repeatableEntry {

      }
    }
  }

  .addButtonPanel {

    padding: 1rem 1.5rem;
    text-align: center;

    i {
      font-size: 1.4rem;
      line-height: 0.1rem;
      position: relative;
      top: 0.3rem;
      left: -0.5rem;
    }

    .addButton {
      background-color: white;
      border: 1px solid $theme-input-border-color;
      padding: 0.5rem 1.1rem 0.2rem;
      border-radius: 0.5rem;
      &:hover, &:active, &:focus {
        background-color: $theme-input-hover-background-color;
      }
    }
  }
}
