

my-screen-portlet {
}

.screenInstance {
  @import "ScreenComponent";
  position: relative;
  width: 100%;
  height: 100%;
  overflow: auto;
  .viewCanvas {
    width: 100%;
    // height: 100%;
    overflow: auto;
    user-select: none;

    > .my-screen-component {
      position: relative;
      top: unset !important;
      left: unset !important;
      width: unset !important;
      height: unset !important;
      margin-top: unset !important;
    }

    .componentLabel {
      pointer-events: none;
    }
  }

  .allErrorsInfo {
    position: fixed;
    right: 1rem;
    bottom: 1rem;
    background-color: $theme-color-error;
    padding: 1rem 1rem 1rem 0.7rem;
    border-radius: 0.5rem;
    box-shadow: 0 0 1rem rgba(0,0,0,0.15);
    color: white;

    .errorsLabel {
      padding: 0 0.3rem 0.2rem 0.3rem;
      font-weight: bold;
      font-size: 1.1rem;
      margin-bottom: 0.3rem;
    }

    .errorInfo {
      display: block;
      text-align: left;
      background-color: transparent;
      border: none;
      padding: 0.3rem 0.3rem 0.2rem 0.3rem;

      .definitionId, .refId {
        display: none;
      }

      .errorsIcon {
        position: relative;
        top: 0.2rem;
        margin-right: 0.5rem;
        font-size: 1.2rem;
      }

      .componentName {
        display: inline-block;
        font-weight: bold;
        margin-right: 0.5rem;
      }
      .errorMessage {
        display: inline-block;
      }
      .errorMessages {
        display: inline-block;
        .errorMessage {
          margin-left: 0.7rem;
          &:first-child {
            margin-left: 0;
          }
        }
      }
    }
  }

  .debugButton {
    position: absolute;
    top: 0.2rem;
    right: 0.2rem;
    background-color: transparent;
    width: 2.2rem;
    height: 2rem;
    font-size: 1.5rem;
    text-align: center;
    z-index: 1;
    border: none;
    &:hover, &:focus, &:active {
      background-color: rgba(0,0,0,0.1);
    }
  }

}

@import "common/screenCanvas";

@import "./global";
