.ViewComponent {
  position: relative;

  &.error {
    outline: 2px solid transparentize($theme-color-error, 0.4);
    outline-offset: -2px;
  }
  // alternative way of implementing outline
  //&.validationError:after {
  //  width: 100%;
  //  height: 100%;
  //  content: '';
  //  outline: 2px solid $theme-color-warning;
  //  outline-offset: -2px;
  //  position: absolute;
  //  top: 0;
  //  left: 0;
  //  z-index: 10;
  //}

  .inProgressGlass {
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    width: min-content;
    height: min-content;
    pointer-events: none;

    background-color: transparentize($theme-color-a, 1);

    animation: blink 1.3s;
    animation-iteration-count: infinite;

  }
  @keyframes blink { 0% { background-color:transparentize($theme-color-a, 1)} 40% { background-color:transparentize($theme-color-a, 1)} 70% { background-color:transparentize($theme-color-a, 0.8); }  }


  .errorsInfo {
    position: absolute;
    top: 0;
    right: 0;
    cursor: pointer;
    pointer-events: none;
    display: block !important;

    > .errorsIcon {
      pointer-events: auto;
      display: inline-block;
      background-color: $theme-color-error;
      padding: 0.2rem;
      color: white;
      position: absolute;
      top: 0;
      right: 0;
    }


    &:hover {
      .errorsDetails {
        display: block;
      }
    }

  }

}
