/** FROM INSPINIA */

html {
  position: absolute;
  height: 100%;
  width: 100%;
  overflow: auto;
}

button, input, select, textarea {
  font-family: inherit;
  font-size: inherit;
  line-height: inherit;
}

button, input, optgroup, select, textarea {
  color: inherit;
  font: inherit;
  margin: 0;
}


button, select {
  text-transform: none;
}

button, html input[type=button], input[type=reset], input[type=submit] {
  -webkit-appearance: button;
  cursor: pointer;
}

*, :after, :before {
  box-sizing: border-box;
}

/** FROM INSPINIA END */


