.dateInputPopup {
  background: white;
  border: 1px solid $theme-input-border-color;
  box-shadow: 0 1.5rem 2rem $theme-default-shadow-color;
  z-index: 10000;
  color: $theme-default-font-color;
  padding: 1rem;
  row-gap: 1rem;
  max-width: 25rem !important;
  min-width: 25rem !important;
  overflow: auto;

  >.actions {
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
    flex-grow: 0;
    flex-shrink: 0;
    >.actionButton {
      background-color: transparent;
      border: none;
      padding: 0.5rem;

      &:hover {
        background-color: $theme-input-hover-background-color;;
      }
    }
  }

  >.calendar {

    flex-grow: 0;
    flex-shrink: 0;

    >.calendarHeader {
      display: grid;
      grid-template-columns: 20% 60% 20%;
      justify-items: center;

      .currentValueButton, .prevMonthButton, .nextMonthButton {
        display: inline-block;
        text-align: center;
        border: none;
        font-weight: bold;
        background-color: transparent;
        padding: 0.5rem 1rem;

        &:hover {
          background-color: $theme-input-hover-background-color;;
        }
      }

    }

    >.calendarContent {
      text-align: center;
      padding-top: 1rem;

      >.weeksHeader {
        span {
          font-weight: bold;
          display: inline-block;
          text-align: center;
          width: 3rem;
          height: 2rem;
        }
      }

      >.weeksContent {

        >.week {
          >.dayButton {
            text-align: center;
            width: 3rem;
            height: 3rem;
            background-color: transparent;
            border: none;
            border-radius: 0.4rem;
            font-size: 1.1rem;
            color: black;

            &:hover {
              background-color: $theme-input-hover-background-color;
            }

            &.other {
              color: $theme-lighter-font-color;
            }

            &.selected {
              background-color: $theme-color-a;
              color: white;
            }
          }
        }
      }
    }
  }
}
