my-tabs-container {
  .TabsContainer {

    width: 100%;
    height: 100%;
    position: relative;

    .tabsList {

      display: block;
      height: 2.5rem;
      padding: 0 2rem;

      .tabEntry {
        border: none;
        background-color: transparent;
        display: inline-block;
        font-size: 1.1rem;
        font-weight: bold;
        padding: 0.5rem 1rem 0.2rem;
        color: $theme-lighter-font-color;
        text-transform: uppercase;
        letter-spacing: -.001rem;
        cursor: pointer;
        position: relative;
        left: -1rem;

        transition: background 0.2s;

        span {
          display: block;
          padding: 0 0.1rem;
          border-bottom: 2px solid transparent;
        }

        &:last-child {
          margin-right: 0;
        }

        &:active, &:focus, &:hover {
          color: $theme-font-color-a;
        }

        &:active, &:hover {
          transition: background 0.5s;
          background-color: rgba(0,0,0,0.05);
        }

        &.active {
          color: $theme-font-color-a;
          span {
            border-color: $theme-font-color-a;
          }
        }
      }
    }

    .tabContent {
      display: block;
      height: calc(100% - 2.5rem);
      position: relative;
    }


  }
}
