.PersonSelectComponent {
  display: flex;
  flex-direction: column;
  color: $theme-lighter-font-color;

  &.isTextColorSet {
    .organizationNode {
      //  If text color has ben set by the designer use it (inherit) - some nod types has their own font color set by css
      color: inherit !important;
    }
  }

  .organizationNodeSelect {
    position: relative;

    .selectedNode {
      background-color: transparent;
      border-width: 0;
      overflow: visible;

      .noNode {
        padding: 0;
      }

      .organizationNode {
        .nodeIcon {
          line-height: .8; // Fix selected group and department height
        }

        &.personNode {
          overflow: visible;

          .nodeIcon {
            position: absolute;
            top: -0.5rem;
          }

          .nodeDescription {
            margin-left: 3rem;
          }
        }
      }
    }
    .searchBox {
      background-color: transparent;
      position: absolute;
      height: auto;

      .organizationNodesSearchBox {
        .nodesQuery {
          border-width: 0;
        }
        i.searchIcon {
          color: inherit;
        }
      }
    }

    .selectedNode {
      color: inherit;
    }

  }

  .input {
    border: 1px solid #e5e6e7;
    // display: flex;
    flex-grow: 1;
    resize: none;
    padding: 0.7rem 0.3rem 0.5rem;
    // line-height: 1.4em;
    overflow: hidden;
    box-shadow: inset 0 0.1rem 0.3rem rgba(0, 0, 0, 0.05);
    border-radius: 0.3rem;
    outline: 0;
    background-color: white;

    &:focus, &:focus-within {
      transition: border-color 0.15s ease-in-out 0s, box-shadow 0.15s ease-in-out 0s, background-color 0.2s;
      border-color: #20b599;
    }

    &.disabled, &:disabled {
      border: 1px solid #f0f0f0;
      background-color: #fcfcfc;
      box-shadow: none;
      cursor: default;
    }

  }

  .expandArrow {
    position: absolute;
    top: calc(50% + 0.3rem);
    right: 0.8rem;
    font-size: 1.4rem;
    transform: translateY(-50%);
    pointer-events: none;
  }

  &.preview {
    .input {
      border-color: transparent;
      box-shadow: none;
    }
  }

}
