.NumberInputComponent {

  position: relative;

  &.preview {
    .input {
      width: fit-content;
      flex-grow: 0;
      padding-right: 0.4rem !important;
    }

    .unitName {
      position: relative;
    }
  }


  &.validationError {
    .input {
      border-color: $theme-color-error !important;
      color: $theme-color-error !important;
    }
  }

  .input {
    user-select: text;
  }

  input.input, textarea.input {
    display: block;
    min-height: 100%;
    height: 1px;
    box-shadow: inset 0 0.1rem 0.3rem rgba(0, 0, 0, 0.05);

    &.changed1 {
      background-color: desaturate(lighten($theme-color-a, 30%), 30%) !important;
      transition: background-color 0.2s;
    }

    &.changed2 {
      background-color: white;
      transition: background-color 3s;
    }

  }

  input.input {
    height: auto; // Fix non-multiline element default height
  }

  .unitName {
    position: absolute;
  }

}
