.MultiAttachmentInputComponent {

  &.validationError {
    border-color: $theme-color-error !important;
  }

  .entryPlaceholder {
    height: 1.5rem;
    width: 70%;
    background-color: $theme-background-color;
    margin: 0.7rem 1rem;
  }

  .fileUploadDisplay {

    .dropAreaGlass {
      display: none;
      border: 2px dashed $theme-drop-area-border-color;
      background: $theme-drop-area-background-color;
      position: absolute;
      top: -0.3rem;
      left: -0.35rem;
      right: -0.3rem;
      bottom: -0.3rem;
      pointer-events: none;
      z-index: 1000;

      .dropInstruction {
        font-size: 2rem;
        color: $theme-drop-area-message-color;
        position: absolute;
        left: 50%;
        top: 50%;
        -ms-transform: translate(-50%, -50%);
        -moz-transform: translate(-50%, -50%);
        -webkit-transform: translate(-50%, -50%);
        transform: translate(-50%, -50%);
      }
    }

    .uploadProgress {
      position: absolute;
      top: 0;
      left: 0;
      bottom: 0;
      background-color: $theme-color-a-light;
      opacity: 0.3;
      pointer-events: none;
    }

    &.fileDropZone {
      .dropAreaGlass {
        display: block;
      }
    }

    &.fileDragged {
      .dropAreaGlass {
        display: block;
        background: $theme-drop-area-hover-background-color;
      }
    }



  }

  .filesContainer {

    .singleAttachment {
      &:hover {
        // background-color: $theme-light-hover-color;
      }


    }


    .fileInfo {
      .fileName {
        &:hover, &:active, &:focus {
          .nameOnly {
            text-decoration: underline;
          }
        }
      }

      .fileDownload {
        color: $theme-lighter-font-color;
        font-size: 1.5rem;
        margin-right: 0.6rem;
        text-decoration: none;

        i {
          position: relative;
          top: 0.3rem;
        }
        &:hover, &:active, &:focus {
          color: $theme-default-font-color;
        }
      }
    }

    .emailInfo {
      cursor: pointer;
    }
  }



  .deleteButton, .cancelButton {
    position: absolute;
    background: transparent;
    border: none;
    color: $theme-lighter-font-color;

    &:hover, &:active, &:focus {
      color: $theme-default-font-color;
    }
  }

  .deleteButton {
    right: 0.3rem;
    top: 0.8rem;
    font-size: 1.4rem;
  }

  .cancelButton {
    right: 0.1rem;
    top: 0.4rem;
    font-size: 2rem;
  }


  .uploadSection {
    .uploadButton {

      &:hover, &:active, &:focus {
        background-color: $theme-input-hover-background-color;
        color: $theme-default-font-color;
      }
    }
  }


}
