.MultiAttachmentInputComponent {
  min-height: 7.2rem;
  border-radius: 0.3rem;
  border: 1px solid $theme-input-border-color;
  display: flex;
  background-color: white;
  overflow: hidden;

  &.disabled {
    border: 1px solid #f0f0f0;
    background-color: $theme-disabled-input-background-color;
    .fileUploadDisplay {
      box-shadow: none;
    }
  }

  &.preview {
    border: 1px solid transparent;
    box-shadow: none;
    background-color: transparent;
  }

  .dragArea {
    flex-grow: 1;
    display: flex;
    flex-direction: row;
    flex-wrap: nowrap;
  }

  .fileName {
    &:hover, &:active, &:focus {
      .nameOnly {
        text-decoration: underline;
      }
    }
  }

  .inputHeader {
    background-color: $theme-background-color;
    width: 3rem;
    padding-top: 0.7rem;
    font-size: 1.5rem;
    text-align: center;
    border-right: 1px solid $theme-input-border-color;
  }



  .filesContainer {
    flex-grow: 1;

    .singleAttachment {
      position: relative;
      border-bottom: 1px solid $theme-input-border-color;

      .fileInfo {
        display: block;
        // padding: 0.4rem 1.1rem 0.8rem;
        font-size: 0.9rem;
        color: $theme-lighter-font-color;

        .uploadedSize {
          color: $theme-default-font-color;
        }

        .fileName {
          text-decoration: none;
          font-size: 1.1rem;
          padding: 0.7rem 0.8rem;
          display: inline-block;
          color: $theme-font-color-b;


          i.fileIcon {
            font-size: 1.8rem;
            line-height: 1rem;
            position: relative;
            top: 0.6rem;
            margin-right: 0.5rem;
          }
        }

        .details {
          font-size: 0.9rem;
          margin-left: 1rem;
          color: $theme-lighter-font-color;
          .separator {
            margin: 0 0.5rem;
          }
        }

      }

      .directoryInfo {

      }

      .flowInfo {
        width: 100%;
        border: none;
        background-color: transparent;
        text-align: left;
        color: $theme-font-color-b;
        padding: 0;

        .processStatus {
          font-size: 0.9rem;
          color: $theme-lighter-font-color;
          text-transform: uppercase;
        }

        .flowCode {
          font-size: 1.1rem;
          padding: 0.7rem 0.8rem;
          display: inline-block;


          i.flowIcon {
            font-size: 1.8rem;
            line-height: 1rem;
            position: relative;
            top: 0.6rem;
            margin-right: 0.5rem;
          }
        }
      }

      .businessEntityInfo {
        width: 100%;
        border: none;
        background-color: transparent;
        text-align: left;
        color: $theme-font-color-b;
        padding: 0;
      }

      .unknownInfo {
        width: 100%;
        border: none;
        background-color: transparent;
        text-align: left;
        color: $theme-font-color-b;
        font-size: 1.1rem;
        padding: 0.7rem 0.8rem;

        i {
          line-height: 1rem;
          position: relative;
          top: 0.6rem;
          font-size: 1.8rem;
        }
      }

      .emailInfo {
        font-size: 0.9rem;
        width: 100%;
        border: none;
        background-color: transparent;
        text-align: left;
        color: $theme-font-color-b;
        padding: 0;

        .emailData {
          font-size: 1.1rem;
          padding: 0.7rem 0.8rem;
          display: inline-block;



          .details {
            color: $theme-lighter-font-color;
            font-size: 0.9rem;
            margin-left: 1rem;
            .separator {
              margin: 0 0.5rem;
            }

            .created {

            }
          }
          .emailIcon {
            margin-right: -0.3rem;
            line-height: 1rem;
            position: relative;
            top: 0.6rem;
            .mainEmailIcon {
              font-size: 1.8rem;
            }
            .emailDirectionIcon {
              position: relative;
              left: -0.4rem;
              top: -0.3rem;
              font-size: 1.1em;
            }
          }

        }

      }

    }
  }

  .uploadSection {
    text-align: right;
    // margin-top: 0.3rem;
    flex-grow: 0;

    .uploadButton {
      display: inline-block;
      border: none;
      background-color: transparent;
      font-size: 1rem;
      padding: 0.7rem 1rem;
      color: $theme-lighter-font-color;


      i {
        font-size: 1.7rem;
        line-height: 1rem;
        position: relative;
        top: 0.4rem;
        margin-right: 0.5rem;
      }
    }

  }

  .fileUploadDisplay {
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: column;
    box-shadow: inset 0 0.1rem 0.3rem rgba(0, 0, 0, 0.05);
  }

  &.minimal {
    .fileUploadDisplay {
      flex-direction: row;
      flex-wrap: nowrap;
      justify-content: stretch;
      .filesContainer {
        .singleAttachment {
          &:last-child {
            border-bottom: none;
          }
        }
      }

      .uploadSection {
        margin-top: 0;
        border-left: 1px solid $theme-box-border-color;
        .uploadButton {
          width: 100%;
          height: 100%;
          padding: 0 0.2rem;
          i {
            margin-right: 0;
          }
        }
      }

    }
  }

  &.singleLine {
    .filesContainer {
      .singleAttachment {

      }
    }
  }

}
