.RadioButtonComponent {
  .radioEntry {
    position: relative;
    background-color: transparent;
    border: none;
    cursor: default;
    display: block;
    padding: 0.5em 0 0.2em 0;
    min-height: 2em;
    min-width: 2em;

    .radioButtonSvg {
      position: absolute;
      top: 0.5rem;
      left: 0.2rem;
      width: 1.6em;
      height: 1.6em;
      display: inline-block;
      vertical-align: middle;

      .outsideCircle {
        fill: #fff;
      }

      .path {
        fill: #dee1e3;
      }

      .insideCircle {
        visibility: hidden;
      }
    }

    .radioButtonLabel {
      vertical-align: top;
      color: $theme-lighter-font-color;
      margin: 0 1em 0 2em;
      white-space: normal;
      line-height: 1.77em;
      text-align: left;
    }

    &.checked {

      .radioButtonSvg {

        .outsideCircle {
          fill: #a8a8aa;
        }

        .path {
          fill: #9a9a9d;
        }

        .insideCircle {
          visibility: visible;
          fill: #fff !important;
        }
      }

      .radioButtonLabel {
        color: $theme-default-font-color;
      }
    }
  }

  &.invalid {
    .radioEntry {
      .radioButtonSvg {
        .path {
          fill: $theme-color-error !important;
        }
      }

      .radioButtonLabel {
        color: $theme-color-error !important;
      }
    }
  }

  &.enabled {

    .radioEntry {
      cursor: pointer;
      &:focus {
        .radioButtonSvg {
          .path {
            fill: #bbb;
          }
        }

        .radioButtonLabel {
          color: $theme-medium-font-color;
        }
      }

      &:hover {
        .radioButtonSvg {
          .path {
            fill: #bbb;
          }
        }

        .radioButtonLabel {
          color: $theme-medium-font-color;
        }
      }

      .radioButtonSvg {
        .outsideCircle {
          fill: #fff;
        }
      }


      &.checked {

        .radioButtonSvg {

          .outsideCircle {
            fill: $theme-button-color-a;
          }

          .path {
            fill: $theme-button-color-a;
          }
        }


        &:focus {
          .radioButtonSvg {
            .path {
              fill: #777;
            }
          }

          .radioButtonLabel {
            color: $theme-default-font-color;
          }
        }

        &:hover {
          .radioButtonSvg {
            .path {
              fill: #777;
            }
          }

          .radioButtonLabel {
            color: $theme-default-font-color;
          }
        }
      }
    }
  }











}


