html, body, div, span, applet, object, iframe, h1,
h2, h3, h4, h5, h6, p, blockquote, pre, a, abbr, acronym, address, big,
cite, code, del, dfn, em, font, img, ins, kbd, q, s, samp, small, strike,
strong, sub, sup, tt, var, b, u, i, center, dl, dt, dd, ol, ul, li, fieldset, form, label, legend, caption {
  margin: 0;
  padding: 0;
  border: 0;
  outline: 0;
  vertical-align: baseline;
  background: transparent;
}

textarea {
  resize: none;
  white-space: pre-line;
}

a::-moz-focus-inner, button::-moz-focus-inner, input[type="button"]::-moz-focus-inner, input[type="submit"]::-moz-focus-inner, input[type="reset"]::-moz-focus-inner {
  padding: 0 !important;
  border: 0 none !important;
}

a, button, input[type="button"], input[type="submit"], input[type="reset"] {
  outline: none !important;
  outline-style: none !important;
  -moz-outline-style: none !important;
}

a.navigable {
  &:focus {
    text-decoration: underline;
  }

  &:hover {
    text-decoration: underline;
  }
}

* {
  box-sizing: border-box;
}

input[type="text"], button, input[type="button"], input[type="submit"], input[type="reset"], textarea {
  box-sizing: border-box;
}

input::-ms-clear {
  display: none;
}

option {
  background-color: white !important; // otherwise highlighted select option is not visible
}
